
Template.__checkName("notification_post_approved");
Template["notification_post_approved"] = new Template("Template.notification_post_approved", (function() {
  var view = this;
  return HTML.P("\n    ", Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "your_post");
  }), " \n    ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("postUrl"));
    },
    "class": "action-link"
  }, Blaze.View("lookup:postTitle", function() {
    return Spacebars.mustache(view.lookup("postTitle"));
  })), "\n    ", Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "has_been_approved");
  }), " \n  ");
}));
