
Template.__checkName("notification_item");
Template["notification_item"] = new Template("Template.notification_item", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return Blaze.If(function() {
        return Spacebars.call(Spacebars.dot(view.lookup("item"), "data", "read"));
      }, function() {
        return "read";
      });
    }
  }, "\n    ", HTML.SPAN({
    "class": "notification-timestamp"
  }, Blaze.View("lookup:timeAgo", function() {
    return Spacebars.mustache(view.lookup("timeAgo"), Spacebars.dot(view.lookup("item"), "data", "timestamp"));
  }), ":"), "\n    ", HTML.DIV({
    "class": "notification-html"
  }, "\n      ", Blaze.View("lookup:notificationHTML", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("notificationHTML")));
  }), "\n    "), "\n  ");
}));
