
Template.__checkName("notification_new_reply");
Template["notification_new_reply"] = new Template("Template.notification_new_reply", (function() {
  var view = this;
  return HTML.P("\n    ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("profileUrl"));
    }
  }, Blaze.View("lookup:authorName", function() {
    return Spacebars.mustache(view.lookup("authorName"));
  })), "\n    ", Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "has_replied_to_your_comment_on");
  }), " \n    ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("commentUrl"));
    },
    "class": "action-link"
  }, Blaze.View("lookup:postTitle", function() {
    return Spacebars.mustache(view.lookup("postTitle"));
  })), "\n  ");
}));
