
Template.__checkName("notifications_menu");
Template["notifications_menu"] = new Template("Template.notifications_menu", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("isLoggedIn"));
  }, function() {
    return [ "\n      ", HTML.DIV({
      "class": function() {
        return [ "notifications-menu-wrapper ", Spacebars.mustache(view.lookup("moduleClass")) ];
      }
    }, "\n        ", Blaze._TemplateWith(function() {
      return {
        menuName: Spacebars.call("notifications"),
        menuLabel: Spacebars.call(view.lookup("menuLabel")),
        menuItems: Spacebars.call(view.lookup("menuItems")),
        menuType: Spacebars.call(view.lookup("menuType")),
        expandLevel: Spacebars.call(0)
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("menuComponent"));
    }), "\n      "), "\n    " ];
  });
}));
